.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: rgba(33, 106, 101, 1);
}

.nav-link {
    float: left;
    color: #f2f2f2;
    background-color: rgba(33, 106, 101, 1);
    text-align: center;
    padding: 24px 10px;
    text-decoration: none;
    font-size: 15px;
  
    /* these last two make the logout button in
    the nav bar look like another link */
    border:none;
    cursor: pointer;
  }
  
  .nav-link:hover {
    background-color: #008183;
  }
  
  .nav-link.active {
    background-color: #4CAF50;
    color: white;
  }
  
  .nav-right {
    float: right;
  }