.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: rgba(33, 106, 101, 1);
}

.nav-link {
    float: left;
    color: #f2f2f2;
    background-color: rgba(33, 106, 101, 1);
    text-align: center;
    padding: 24px 10px;
    text-decoration: none;
    font-size: 15px;
  
    /* these last two make the logout button in
    the nav bar look like another link */
    border:none;
    cursor: pointer;
  }
  
  .nav-link:hover {
    background-color: #008183;
  }
  
  .nav-link.active {
    background-color: #4CAF50;
    color: white;
  }
  
  .nav-right {
    float: right;
  }
footer {
    margin-top: 80px;
}

body {
    font-family:
    'Avenir-Book', 'Avenir Book', 'Avenir';
      
    /* 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

.customForm {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

.center-image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    
}

